<template>
  <div class="wrapper">

    <div class="meeting-container-timer">
      <TimerComponent :timing="currentTiming"/>
    </div>

    <div class="meeting-container">
      <div class="meeting-controls">
        <v-btn
            color="green-darken-1"
            @click="changeMeeting('rm')"
            size="small"
        >
          РМ
        </v-btn>

        <v-btn
            color="deep-purple accent-4"
            @click="changeMeeting('light')"
            size="small"
        >
          Освещение
        </v-btn>

        <v-btn
            color="pink-accent-1"
            @click="changeMeeting('dish')"
            size="small"
        >
          Фарфор
        </v-btn>

        <v-btn
            color="indigo-accent-4"
            @click="changeMeeting('sales')"
            size="small"
        >
          Отдел продаж (18)
        </v-btn>

        <v-btn
            color="teal-lighten-1"
            @click="changeMeeting('steel')"
            size="small"
        >
          Металл
        </v-btn>

        <v-btn
            color="orange-accent-4"
            @click="changeMeeting('top')"
            size="small"
        >
          Отдел продаж (22)
        </v-btn>
      </div>
      <StockMeetingComponent v-if="currentMeeting === 'top'"/>
      <RmMeetingComponent v-if="currentMeeting === 'rm'"/>
      <LightMeetingComponent v-if="currentMeeting === 'light'"/>
      <SalesMeetingComponent v-if="currentMeeting === 'sales'"/>
      <DishMeetingComponent v-if="currentMeeting === 'dish'"/>
      <SteelMeetingComponent v-if="currentMeeting === 'steel'"/>
    </div>

  </div>

</template>

<script>
import TimerComponent from "@/components/others/TimerComponent.vue";
import StockMeetingComponent from "@/components/others/StockMeetingComponent.vue";
import RmMeetingComponent from "@/components/others/RmMeetingComponent.vue";
import LightMeetingComponent from "@/components/others/LightMeetingComponent.vue";
import SalesMeetingComponent from "@/components/others/SalesMeetingComponent.vue";
import DishMeetingComponent from "@/components/others/DishMeetingComponent.vue";
import SteelMeetingComponent from "@/components/others/SteelMeetingComponent.vue";

export default {
  name: "PlanFactComponent",
  components: {
    SalesMeetingComponent,
    LightMeetingComponent,
    StockMeetingComponent,
    TimerComponent,
    RmMeetingComponent,
    DishMeetingComponent,
    SteelMeetingComponent
  },
  data() {
    return {
      currentTiming: 85,
      currentMeeting: 'rm',
    }
  },
  methods: {
    changeMeeting(meeting) {
      if (meeting === 'rm') {
        this.currentTiming = 85;
        this.currentMeeting = 'rm';
      } else if (meeting === 'light') {
        this.currentTiming = 65;
        this.currentMeeting = 'light';
      } else if (meeting === 'sales') {
        this.currentTiming = 60;
        this.currentMeeting = 'sales';
      } else if (meeting === 'top') {
        this.currentTiming = 60;
        this.currentMeeting = 'top';
      } else if (meeting === 'dish') {
        this.currentTiming = 65;
        this.currentMeeting = 'dish';
      } else if (meeting === 'steel') {
        this.currentTiming = 110;
        this.currentMeeting = 'steel';
      }
    }
  },
  mounted() {
    localStorage.setItem('checkLoadStatus', 'loaded');
  },
  beforeUnmount() {
    localStorage.removeItem('checkLoadStatus');
  }
}
</script>

<style lang="scss" scoped>

.wrapper {
  position: relative;

  .meeting-container-timer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: fixed;
  }

  .meeting-container {
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    background-color: snow;
    overflow: hidden;
    margin-left: 280px;

    .meeting-controls {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      align-items: center;
      flex-wrap: wrap;

      .v-btn {
        flex-grow: 1;
        border-radius: 0;
      }
    }
  }
}


</style>